.titleheaderabout {
  height: 56px;
  font-family: Dosis;
  font-style: normal;
  font-weight: 600;
  font-size: 48px;
  line-height: 56px;
}
.textabout {
  max-width: 779px !important;
  min-height: 96px !important;
  margin-top: 12px !important;
  opacity: 0.7 !important;
  /* font-family: 'Jost !important; */
  font-style: normal !important;
  font-weight: 300 !important;
  font-size: 26px !important;
  margin-bottom: 10px !important;
  line-height: 32px !important;
}
.ImagesAboutUs {
  display: flex !important;
  height: 296px !important;
  scrollbar-width: none !important;
  row-gap: 20px !important;
  scroll-snap-type: x mandatory !important;
  scroll-snap-align: start !important;
  overflow-x: scroll !important;
  max-width: 2400px !important;
  overflow-y: hidden !important;
  padding-right: 50px !important;
  scrollbar-width: none !important;
  /* animation-name: primary1;
  animation-duration: 4s; */
  /* width: 975px !important; */
  /* scroll-snap-align: start !important; */
  /* animation: primary1 15s alternate infinite !important;
  -webkit-animation: primary1 15s alternate infinite !important; */
}
.ImagesAboutUs::-webkit-scrollbar {
  display: none;
}
.imagesaboutussa {
  scroll-snap-align: center !important;
  margin: 10px !important;
  height: 200px !important;
  border-radius: 10px !important;
  position: relative !important;
  width: 400px !important;
  object-fit: cover !important;
  /* animation-name: example; */
  background-color: aqua;
  /* animation-duration: 3s; */
  /* -webkit-animation: primary1 15s alternate infinite !important; */
}

.imagesaboutussa {
  animation-name: example;
  animation-timing-function: ease-in-out !important;
  animation-duration: 15s !important;
  animation-direction: alternate !important;
  display: flex !important;
  position: relative !important;
  animation-iteration-count: infinite !important;
}
/* .imagesaboutussa:hover,.imagesaboutussa:active{
  animation-play-state: paused !important;
} */
@keyframes example {
  from {
    transform: translateX(0px);
  }
  to {
    transform: translateX(-1200px);
  }
}
